import { _decorator, Component, Node, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('scrollControl')
export class scrollControl extends Component {
    private touchStartPos:Vec3;
    start() {
        this.node.on(Node.EventType.TOUCH_START,this.touchStart,this);
        this.node.on(Node.EventType.TOUCH_END,this.touchEnd,this)
    }

    update(deltaTime: number) {
        
    }

    touchStart(event){
        this.touchStartPos = event.getLocation()
    }

    touchEnd(event){
        if(this.touchStartPos == null){
            return
        }
        let distance = Math.abs (event.getLocation().x - this.touchStartPos.x);
        console.log("滑动距离为：",distance)

        if(event.getLocation().x < this.touchStartPos.x){
            console.log("向左滑动")
        }
        if(event.getLocation().x < this.touchStartPos.x){
            console.log("向右滑动")
        }

    }
}


